import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthProvider } from '../../providers/auth/auth';
import { ApplyPage } from '../apply/apply';

@IonicPage()
@Component({
    selector: 'page-login',
    templateUrl: 'login.html',
})
export class LoginPage {
    form: FormGroup;

    constructor(
        public navCtrl: NavController,
        private fb: FormBuilder,
        private loadingCtrl: LoadingController,
        private auth: AuthProvider,
        public navParams: NavParams) {

        this.form = this.fb.group({
            email_id: ['', Validators.required],
            password: ['', [Validators.required]],
            quote: ['', [Validators.required]],
        });
    }

    getQuote() {
    const loader = this.loadingCtrl.create({
      content: "Please wait...",
      duration: 3000
    });
    loader.present();

        this.auth.getQuote(this.form.value)
        .then((resp:any) => {
               loader.dismiss();
            if(resp.message_error)
            {

            }
            else
            {

            }
        }, error => {
            loader.dismiss();
        })
    }

    login() {
    const loader = this.loadingCtrl.create({
      content: "Please wait...",
      duration: 3000
    });
    loader.present();

    	this.auth.login(this.form.value)
    	.then((resp:any) => {
    		console.log(resp)
   			loader.dismiss();
    		if(resp.message_error)
    		{

    		}
    		else
    		{
    		   localStorage.setItem('token',resp.token);
    		   localStorage.setItem('comp_name',resp.comp_name);
    		   localStorage.setItem('email_id',resp.email_id);
    		   localStorage.setItem('is_admin',resp.is_admin);
    		   localStorage.setItem('is_logged_in',resp.is_logged_in);
    		   this.navCtrl.setRoot(ApplyPage)
    		}
    	}, error => {
    		loader.dismiss();
    		console.log(error)
    	})
    }

}


