import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastController } from 'ionic-angular';
import { Injectable } from '@angular/core';
import * as Rx from "rxjs";
import { map } from 'rxjs/operators';

@Injectable()
export class AuthProvider {

public apiUrl: any = 'http://linksfactoring.ignitershub.com/customer/api/1'

  constructor(
  	public http: Http,
	public toastCtrl: ToastController,
  	) {
    console.log('Hello AuthProvider Provider');
  }

  login(form){
        let that = this
        return new Promise((resolve, reject) => {
		  	this.http.post(this.apiUrl+'/user/auth', form)
		  	.pipe(map(x => x.json()))
		  	.subscribe(resp => {
		  		console.log("resp ",resp)
		  		resolve(resp);
		  	}, error => {
		  		console.log("error ",error)
		  		reject(error);
		  	})
        });
  }

  register(form){
        let that = this

        let headers=new Headers({
        	'Content-Type': 'multipart/form-data;', 
        });
   		 let options = new RequestOptions({ headers: headers });

		  		console.log("form ",form)

        return new Promise((resolve, reject) => {
		  	this.http.post(this.apiUrl+'/user/register', form)
		  	.pipe(map(x => x.json()))
		  	.subscribe(resp => {
		  		console.log("resp ",resp)
		  		resolve(resp);
		  	}, error => {
		  		console.log("error ",error)
		  		reject(error);
		  	})
        });
  }

  getQuote(form){
        let that = this
        this.presentToast('Quote sent on your email Id!')
		  		let object = {email_id: form.email_id, password: form.password}
		  		console.log("object ",object)
        return new Promise((resolve, reject) => {
		  	this.http.post(this.apiUrl+'/user/quote', object)
		  	.pipe(map(x => x.json()))
		  	.subscribe(resp => {
		  		console.log("resp ",resp)
		  		resolve(resp);
		  	}, error => {
		  		console.log("error ",error)
		  		reject(error);
		  	})
        });
  }

  forgotPassword(form){
        let that = this

        let headers=new Headers({
        	'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundary7MA4YWxkTrZu0gW', 
        });
   		 let options = new RequestOptions({ headers: headers });

		  		console.log("form ",form)

		  		let object = {email_id: form.email_id, password: form.password}
		  		console.log("object ",object)
        return new Promise((resolve, reject) => {
		  	this.http.post(this.apiUrl+'/user/auth', object)
		  	.pipe(map(x => x.json()))
		  	.subscribe(resp => {
		  		console.log("resp ",resp)
		  		resolve(resp);
		  	}, error => {
		  		console.log("error ",error)
		  		reject(error);
		  	})
        });
  }

  presentToast(message) {
    const toast = this.toastCtrl.create({
      message: message,
      position: 'bottom',
      duration: 3000
    });
    toast.present();
  }

}



